import React from 'react'
import Encabezados from '../../Components/Encabezado/Encabezados'
import CarouselMataPalo from '../../Components/Carousel/CarouselMataPalo'
import Formulario from '../../Components/Formularios/Formulario'

const SurfMatapalo = () => {
    return (
        <div className='container-fluid'>
            <Encabezados titulo='Bienvenidos al Tour de Surfing en Mata Palo' info='En esta opcin contamos con Instructor de Surf bilinge o solamente con el trasporte martimo. Playa matapalo es una playa que est ubicada al frente de pavones, su nico trasporte es martimo lo cual para ir ah tenemos que ir en bote, en este recorrido disfrutaremos de la briza del mar, miraremos algunos delfines, tiburones y algunas tortugas, tambin observaremos algunas ballenas si es la temporada. Aparte de ser un tour agradable, disfrutaremos de sus olas.'/>
            <CarouselMataPalo/>
            <Formulario/>
        </div>
    )
}

export default SurfMatapalo
