        info="En este tour usted viajar desde Pavones centro hasta la montaa viendo vida silvestre y animales de la regin hasta llegar al pueblo indgena Altamira, de regreso pasaremos por el Rio donde podremos observar una pequea cascada donde podr descansar, disfrutar del rio y relajarse, adems de darse un delicioso bao. Cuenta con un refrigerio incluido. Duracin aproximada:  4 a 5 hrs."
