import React, { useState } from "react";
import { useForm } from "react-hook-form";

const FormularioContacto = () => {
  const [informes, setInformes] = useState([]);
  const { register, errors, handleSubmit } = useForm();

  const onSubmit = (data, e) => {
    //Guardamos todos los datos de formulario en la variable informe
    setInformes([...informes, data]);
    //Reseteamos el formulario.
    e.target.reset();
  };
  return (
    <div className="col-6">
      <h3 className="text-center">Formulario de Contactanos</h3>
      <form action="" name="formContacto" onSubmit={handleSubmit(onSubmit)}>
        <label htmlFor="correo">Correo:</label>
        <input
          type="text"
          name="correo"
          id="correo"
          className="form-control"
          ref={register({
            required: { value: true, message: "Espacio requerido" },
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: 'Su correo debe contener un "@".',
            },
          })}
        />
        <span className="text-danger d-block mb-2">
          {errors?.correo?.message}
        </span>
        <label htmlFor="telefono">Telfono:</label>
        <input
          type="text"
          name="telefono"
          id="telefono"
          className="form-control"
          ref={register({
            required: { value: true, message: "Espacio requerido" },
            pattern: {
              value: /^[0-9]+$/i,
              message:
                "No escriba signos de admiracin,interrogacin, exclamcin o letras",
            },
          })}
        />
        <span className="text-danger d-block mb-2">
          {errors?.telefono?.message}
        </span>
        <label htmlFor="descripcion">Descripcin:</label>
        <textarea
          className="form-control-plaintext"
          name="descripcion"
          id="descripcion"
          placeholder="Describa su informacin"
          ref={register({
            required: { value: true, message: "Espacio requerido" },
          })}
        />
        <span className="text-danger d-block mb-2">
          {errors?.descripcion?.message}
        </span>
        <button type="submit" className="btn btn-primary">
          Enviar
        </button>
      </form>
      <div className="container">
        <h5>La informacion que le llegaria al correo a ustedes:</h5>
        {informes.map((informe) => (
          <p>
            {`Correo es: ` + informe.correo}-
            {`El telfono: ` + informe.telefono}-
            {`La descripcin es: ` + informe.descripcion}
          </p>
        ))}
      </div>
    </div>
  );
};

export default FormularioContacto;
